@import url(https://fonts.googleapis.com/css?family=Raleway:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: SF-Pro-Text;
  src: url(/static/media/FontsFree-Net-SFProDisplay-Regular.1e1c0e29.ttf);
}

body, #app, #app>div {
  /* height: 100%; */
  background-color: #E0F2FF;
}

.App {
  text-align: center;
  background-color: #E0F2FF;
  font-family: SF-Pro-Text;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  min-height: 10vh;
  background: rgb(111, 111, 165);
}

.nav-links {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  list-style: none;
  font-size: larger;
  font-weight: 500;
}

.nav-link {
  text-decoration: none;
  padding: 0.4rem;
  font-size: 30px;
  color: black;
}

.nav-link:hover, .nav-link:active {
  color: #E0F2FF;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  /* padding-bottom: 0.25rem;
  border-bottom: 1px solid #95bcf0; */
}

.nav-link-active {
  color: #E0F2FF;
}

.authentication-header {
  padding-bottom: 2rem;
}

.authentication-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.username-form {
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 20%;
  padding-bottom: 1rem;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.auth-form {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.input-label {
  padding-right: 1rem;
}

.password-form {
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 20%;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.submit-button {
  background-color: #4CAF50;
  /* Green */
  border: none;
  color: black;
  font-weight: bold;
  padding: 1rem 1.6rem;
  margin: 1rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 0.9rem;
}

.submit-button:hover {
  cursor: crosshair;
}

.stock-management {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.stock-management-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.stock-management-item {
  text-decoration: none;
  margin: 2rem;
}

.stock-management-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.stock-management-item:active {
  color: rgb(111, 111, 165);
}

.allstocks {
  text-align: center;
  font-size: medium;
  font-weight: 700;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.button-stock {
  -webkit-flex-basis: 5rem;
          flex-basis: 5rem;
  font-size: medium;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  padding: 0.35em 1.2em;
  border: 0.1em solid#FFFFFF;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.12em;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  color: black;
  transition: all 0.2s;
  background-color: none;
}

.stock-name-link {
  text-decoration: none;
}

.button-stock:hover {
  cursor: crosshair;
  color: springgreen;
  background: rgb(80, 80, 189);
}

.stock-details-table {
  text-align: center;
  overflow: hidden;
  padding: 1rem;
  margin: auto;
  width: 100%;
  display: table;
  /* border: 1px solid black; */
}

.stock-table-heading {
  /* text-decoration: none; */
  background-color: cadetblue;
  /* border: 1px solid black; */
}

.stock-details-table tr {
  padding-bottom: 2%;
  padding-top: 2%;
  padding-left: 2%;
  margin: 1rem;
  border: 1px solid black;
}

.stock-details-table th {
  border: 1px solid black;
}

.user-management h1 {
  font-size: 55px;
}

.user-management-heading{
  font-size: 30px;
}

.user-buttons {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  text-decoration: none;
  list-style: none;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 2rem;
}

.user-buttons a {
  text-decoration: none;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 2rem;
  font-size: 30px;
}

.user-buttons a:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  color: black;
}

