} */

.stock-suggestions {
    text-decoration: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;

}
.stock-suggestion{
    text-decoration: none;
    padding: none;
    margin: none;
    /* width: 50%; */
    height: 0.7rem;
}

.stock-suggestion-item {
    color: black;
    font-size: 20px;
}
.main-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
}

.welcome-message {
    font-size: 55px;
    font-weight: 700;
    color: black;
    -webkit-flex: 1 2 auto;
            flex: 1 2 auto;
    max-width: 100%;
}

.authentication {
    font-size: medium;
    font-weight: 700;
    color: black;
    -webkit-flex-basis: auto;
            flex-basis: auto;
    -webkit-flex-grow: 2;
            flex-grow: 2;
}

.line-break{
    border: none;
}

.auth-form-buttons{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: center;
            align-content: center;
    /* border: 2px solid #d9eaf7; */
    max-width: 50%;
    max-height: 40%;
    /* background-color: #c1e0f7; */
    padding: 0.6rem;
}

.register-button, .login-button{
    /* text-decoration: none;
    font-size: 30px; */
    background: linear-gradient(to right,green 0%,sand 50%,peach 100%);
    background-size: 500%;
    border: none;
    border-radius: 2rem;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
    color: rgb(14, 13, 13);
    cursor: pointer;
    font-size: 1.5em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: auto;
    letter-spacing: .05em;
    outline: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    /* width: 20rem; */
        /* border: none; */
}
.auth-form-button :hover{
    cursor:crosshair;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}



